
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  height: 100%;
}
*{
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #555d6f;
}

#contents {
  width: 90%;
  height: auto;
  margin: 1px auto;
}
#header {
  padding-top: 10px;
  height: 100%;
  background-color: #555d6f;
  text-align: center;
}
#avatar{
  background-color: #555d6f;
  height: auto;
  padding-bottom: 10px;
}
#avatar img {
  width: 70px;
  height: 70px;
  border: 3px solid #868686;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 35px;
}
#tail {
  margin-top: 5px;
  color: #acacac;
}
.article {
  width: 100%;
  min-height: 400px;
  margin: 0;
  padding: 0;
  border: none;
  background-color: #f0f0f0;
  border-radius:5px;
  margin-top: 5px;
}
#menuBar {
  position: sticky;
  position: -webkit-sticky;
  top: 0px;
}
































